import $http from "../index";

export default {
  getAll: (params) => $http.get("user/", { params }),
  getId: (param) => $http.get(`user/${param}/`),
  add: (params) => $http.post("user/add/", params),
  edit: (params) => $http.put("user/edit/", params),
  delete: (params) => $http.delete(`user/${params}/`),

  getUsersCountByMonth: (params) => $http.get("dashboard/users/", { params }),

  login: (params) => $http.post("user/login/", params),
};
