<template>
  <v-app>
    <v-content fluid fill-height>
      <v-layout align-center justify-center>
        <v-flex xs12 sm8 md4 lg4>
          <v-card>
            <v-toolbar dark color="primary" flat>
              <v-toolbar-title>Administrador</v-toolbar-title>
              <v-spacer />
            </v-toolbar>

            <v-card-text class="pb-0">
              <img
                src="@/assets/images/imps-logo.png"
                :alt="$store.state.appName"
              />

              <v-form
                ref="form"
                v-model="valid"
                @submit.prevent="executeRecaptcha"
              >
                <v-text-field
                  name="app-email"
                  prepend-icon="email"
                  ref="email"
                  label="Digite seu email"
                  type="text"
                  :rules="rules.emailRules"
                  v-model="user.email"
                  autocomplete="no"
                  autofocus
                />
                <v-text-field
                  name="app-pass"
                  prepend-icon="lock"
                  label="Digite sua senha"
                  id="password"
                  type="password"
                  :rules="rules.passwordRules"
                  v-model="user.pass"
                  autocomplete="no"
                />

                <!--
                <vue-recaptcha
                  ref="recaptcha"
                  @verify="recaptchaVerified"
                  @expired="recaptchaExpired"
                  size="invisible"
                  :sitekey="googleRecaptchaKey"
                  v-if="googleRecaptchaKey"
                />
                -->

                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    color="primary"
                    type="submit"
                    :loading="loading"
                    :disabled="!valid"
                    >Acessar</v-btn
                  >
                </v-card-actions>
              </v-form>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-content>

    <Footer />
  </v-app>
</template>

<script>
import LocalStorage from "@/utils/helpers/localStorage";
import Validator from "@/utils/helpers/validator";
import UserService from "@/api/services/user";
import Crypt from "@/utils/helpers/crypt";
// import Noty from "@/utils/helpers/noty";
// import Responses from "@/utils/constants/responses";

export default {
  name: "RestrictedLogin",

  components: {
    // VueRecaptcha: () => import("vue-recaptcha"),
    Footer: () => import("@/components/restricted/commons/Footer"),
  },

  computed: {
    googleRecaptchaKey: () => process.env.VUE_APP_GOOGLE_RECAPTCHA_KEY,
  },

  data: () => ({
    valid: false,
    loading: false,
    user: {
      email: null,
      pass: null,
      gToken: null,
    },
    rules: {
      emailRules: Validator.EmailRules(),
      passwordRules: Validator.PasswordRules(),
    },
  }),

  methods: {
    recaptchaVerified(response) {
      this.user.token = response;
      this.login();
    },

    recaptchaExpired() {
      this.$refs.recaptcha.reset();
      this.loading = false;
    },

    executeRecaptcha() {
      this.loading = true;
      this.$refs.recaptcha.execute();
    },

    async login() {
      if (!this.$refs.form.validate()) {
        return false;
      }

      this.loading = true;
      const params = {
        email: this.user.email,
        pass: new Crypt().CryptPassword(this.user.pass),
        gToken: this.user.token,
      };

      const login = await UserService.login(params)
        .then((response) => {
          LocalStorage.set("authToken", response.data.accessToken);
          this.$router.push({ name: "restricted-dashboard" });
        })
        .catch((r) => {
          if (!r.data || !r.data.msg) {
            // return Noty.error(Responses["error.login"]["pt-BR"]);
          }

          return r;
        })
        .finally(() => {
          this.loading = false;
        });

      return login;
    },
  },

  async created() {
    if (LocalStorage.get("authToken")) {
      this.$router.push({ name: "home" });
    }
  },
};
</script>

<style lang="scss">
.v-content__wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;

  .v-card__text {
    display: flex;
    flex-direction: column;

    img {
      width: 200px;
      height: auto;
      margin: 15px auto;
    }
  }
}
</style>
